html {
  box-sizing: border-box; }

*,
*:before,
*:after {
  box-sizing: inherit; }

body,
button {
  font-family: 'EB Garamond', serif;
  font-size: 1em;
  line-height: 1.6;
  letter-spacing: .0825em; }

a,
button {
  position: relative;
  display: inline-block;
  color: inherit;
  text-decoration: none; }
  a.selected::after, a:hover::after,
  button.selected::after,
  button:hover::after {
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
    -webkit-transform-origin: bottom left;
            transform-origin: bottom left; }
  a::after,
  button::after {
    content: '';
    position: absolute;
    right: 0;
    left: 0;
    bottom: 0;
    height: 1px;
    background-color: #222;
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
    -webkit-transform-origin: bottom right;
            transform-origin: bottom right;
    transition: -webkit-transform .25s ease-out;
    transition: transform .25s ease-out;
    transition: transform .25s ease-out, -webkit-transform .25s ease-out; }
  .is-inverted a::after, .is-inverted
  button::after {
    background: #fefefe; }

h2 {
  font-size: 1.25rem;
  font-weight: 400;
  margin-top: 0;
  margin-bottom: .75em; }

button {
  border: 0;
  background-color: transparent;
  cursor: pointer;
  padding: 0; }

img {
  max-width: 100%;
  height: auto; }

/* 
* Layout 
* 
*/
.wrapper {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  color: #222;
  background-color: #fefefe;
  transition: color .5s ease-in-out, background-color .5s ease-in-out; }
  @media screen and (min-width: 768px) {
    .wrapper {
      display: flex; } }
  .wrapper.is-inverted {
    color: #fefefe;
    background: #222; }

.main {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  margin-right: auto;
  margin-left: auto;
  padding: 1.75em; }
  @media screen and (min-width: 768px) {
    .main {
      width: 44.4%; } }

/* 
* Utils 
* 
*/
.italic {
  font-style: italic; }

@media screen and (max-width: 767px) {
  .no-mobile {
